              justify-content: center;
              .card {
                width: 42%;
                height: 280px;
                background: #ffffff;
                box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.25);
                border-radius: 10px;
                position: relative;
                cursor: pointer;
                margin: 0px 5px;
                .block__card {
                  position: relative;
                  top: 5px;
                  left: 5px;

                  .img {
                    width: 100%;
                    position: absolute;
                    height: 100px;
                    .img__phone {
                      display: none;
                    }
                    .img__tablet {
                      position: absolute;
                      width: 92%;
                      height: 100px;
                      left: 0px;
                      top: 0px;
                      border-radius: 10px;
                    }
                  }

                  h3 {
                    width: 94%;
                    position: absolute;
                    left: 0px;
                    top: 105px;
                    font-family: "Nunito";
                    font-style: normal;
                    font-weight: 400;
                    font-size: calc(
                      14px + (6 + 6 * 0.7) * (100vw - 320px) / 576
                    );
                    line-height: 19px;
                    color: #000000;
                  }

                  p {
                    position: absolute;
                    width: 94%;
                    height: 80px;
                    left: 0px;
                    top: 180px;
                    font-family: "Nunito";
                    font-style: normal;
                    font-weight: 400;
                    font-size: 12px;
                    line-height: 15px;
                  }

                  span {
                    position: absolute;
                    width: 80px;
                    height: 20px;
                    left: 0px;
                    top: 255px;
                    font-family: "Nunito";
                    font-style: normal;
                    font-weight: 400;
                    font-size: 9px;
                    line-height: 20px;
                    color: #000000;
                  }
                }
              }

              .card:hover {
                cursor: pointer;
                box-shadow: 0px 3px 5px 1px #e7813d;
                transition: all 600ms ease-in-out;

                h3 {
                  font-size: calc(15px + (6 + 6 * 0.7) * (100vw - 320px) / 576);
                  transition: all 600ms ease-in-out;
                }

                p {
                  font-size: 13px;
                  transition: all 600ms ease-in-out;
                }
              }
            }
          }
        }
      }
    }

    .arrow__slider {
      width: 100%;
      bottom: 160px;
      .prev {
        img {
          left: -10px;
        }
      }
      .next {
        img {
          right: -10px;
        }
      }
    }
