.section__card {
  h2 {
    font-family: "Nunito";
    font-style: normal;
    font-weight: 700;
    font-size: 40px;
    line-height: 55px;
    color: #000000;
    text-align: center;
  }
  .slider {
    padding-top: 40px;
    position: relative;
    width: 1170px;
    height: 580px;
    overflow: hidden;
    .card__slider {
      transition: all 1s ease;
      width: 100%;
      display: flex;
      .deck__card {
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        .card {
          width: 370px;
          height: 531px;
          background: #ffffff;
          box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.25);
          border-radius: 10px;
          position: relative;
          cursor: pointer;
          .block__card {
            position: absolute;
            top: 35px;
            left: 25px;
            img {
              position: absolute;
              width: 300px;
              height: 185px;
              left: 0px;
              top: 0px;
              border-radius: 10px;
            }
            h3 {
              width: 300px;
              position: absolute;
              left: 0px;
              top: 200px;
              font-family: "Nunito";
              font-style: normal;
              font-weight: 400;
              font-size: 27px;
              line-height: 37px;
              color: #000000;
            }
            p {
              position: absolute;
              width: 300px;
              height: 80px;
              left: 0px;
              top: 320px;
              font-family: "Nunito";
              font-style: normal;
              font-weight: 600;
              font-size: 15px;
              line-height: 20px;
            }
            span {
              position: absolute;
              width: 127px;
              height: 20px;
              left: 190px;
              top: 450px;
              font-family: "Nunito";
              font-style: normal;
              font-weight: 600;
              font-size: 15px;
              line-height: 20px;
              color: #000000;
            }
          }
        }
        .card:hover {
          cursor: pointer;
          box-shadow: 0px 3px 5px 1px #e7813d;
          transition: all 600ms ease-in-out;
          h3 {
            font-size: 29px;
            transition: all 600ms ease-in-out;
          }
          p {
            font-size: 17px;
            transition: all 600ms ease-in-out;
          }
        }
      }
    }
   }
  .arrow__slider {
    position: relative;
    bottom: 310px;
    width: 1170px;
    .prev {
      cursor: pointer;
      img {
        width: 22.5px;
        height: 45px;
        position: absolute;
        left: -50px;
      }
    }
    .next {
      img {
        width: 22.5px;
        height: 45px;
        position: absolute;
        right: -50px;
      }
      cursor: pointer;
    }
    .prev:hover {
      img {
        width: 25px;
        height: 48px;
        transition: all 500ms ease;
      }
    }
    .next:hover {
      img {
        width: 25px;
        height: 48px;
        transition: all 500ms ease;
      }
    }
  }
}
.developer__container {
  h2 {
    font-family: "Nunito";
    font-style: normal;
    font-weight: 700;
    font-size: 40px;
    line-height: 55px;
    text-align: center;
    color: #000000;
  }
  .accordion__list {
      padding-top: 40px;
      display: flex;
      justify-content: space-between;

    .accordion {
      background: #ffffff;
      box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.25);
      border-radius: 15px;
      margin-bottom: 20px;
      .accordion__control {
        display: flex;
        justify-content: space-between;
        padding: 18px 26px;
        width: 570px;
        overflow: hidden;
        cursor: pointer;
        .accordion__title {
          font-family: "Nunito";
          font-style: normal;
          font-weight: 400;
          font-size: 24px;
          line-height: 33px;
          color: #000000;
        }
        .accordion__icon {
          transition: all 0.6s ease;
        }
      }
      .accordion__content {
        max-height: 0;
        opacity: 0;
        overflow: hidden;
        padding: 0 26px;
        will-change: max-height;
        transition: all 0.3s ease-out;
        box-sizing: content-box;
      }
      .accordion__title {
        font-weight: bold;
      }
      .accordion__control {
        cursor: pointer;
      }
      .accordion__left {
        h4 {
          font-family: "Nunito";
          font-style: normal;
          font-weight: 600;
          font-size: 15px;
          line-height: 20px;
          color: #000000;
        }
        ul {
          padding: 12px 0px 0px 10px;
          li {
            font-family: "Nunito";
            font-style: normal;
            font-weight: 600;
            font-size: 15px;
            line-height: 20px;
            text-align: start;
            color: #000000;
            padding: 5px 0px;
          }
        }
      }
      .accordion__right {
        position: relative;
        a {
          font-family: "Nunito";
          font-style: normal;
          font-weight: 600;
          font-size: 15px;
          line-height: 20px;
          text-align: start;
          position: absolute;
          bottom: 0px;
          color: #000000;
        }
      }
    }
    .accordion.open {
      .accordion__content {
        display: block;
        overflow: hidden;
        opacity: 1;
        padding: 26px;
        box-sizing: content-box;
        display: flex;
        justify-content: space-between;
        transition: all 0.3s ease-out;

        .accordion__left {
          h4 {
            font-family: "Nunito";
            font-style: normal;
            font-weight: 600;
            font-size: 15px;
            line-height: 20px;
            color: #000000;
          }
          ul {
            padding: 12px 0px 0px 10px;
            li {
              font-family: "Nunito";
              font-style: normal;
              font-weight: 600;
              font-size: 15px;
              line-height: 20px;
              text-align: start;
              color: #000000;
              padding: 5px 0px;
            }
          }
        }
        .accordion__right {
          position: relative;
          width: 75px;
          a {
            font-family: "Nunito";
            font-style: normal;
            font-weight: 600;
            font-size: 15px;
            line-height: 20px;
            text-align: start;
            position: absolute;
            color: #000000;
          }
        }
      }
      .accordion__icon {
        transform: rotate(-180deg);
        transition: all 0.6s ease;
      }
    }
  }
}
